<script>
	import {Button , Input , Table , TableColumn , Pagination , Dialog , Form , FormItem ,   Radio , RadioGroup , Upload , Select , Option} from 'element-ui'

	export default {
		data(){
			return {
				pageSize:6,
				background:true,
				dialogFormVisible:false,
				goOtherDialogFormVisible:false,
				isShowAddIvBetween:false,
				toLeadDialogFormVisible:false,
				messageDialogFormVisible:false,
				updateTimeDialogFormVisible:false,
				formLabelWidth: '120px',
				goOtherTitle:'下一轮面试',
				degreeDic:{1: '博士', 2: 'MBA' , 3: '硕士' , 4: '本科' , 5: '专科' , 6: '中专/技校/高中' , 7: '高中' , 15: '初中' , 8: '其他'},
				tableData: [{
					Id:0,
					SeatNo:0,
					RootCompanyId:0,
		            DateStart: '2018/1/25  08:00',
		            Name: '刘德华',
		            SchoolName:'清华',
		            Major:'计算机',
		            Degree:1,
		            VideoRoomId:0,
		            Email:'asddggss@163.com',
		            isOnline:true,
		            Position:'产品经理',
		            Status: 0,
		            InvitationCode:'',
		            Mobile:18645618521,
		            Sex:'1',
		            Score:5,
		            Comment:'这个候选人理论上是十分牛逼的这个候选人理论上是十分牛逼的这个候选人理论上是十分牛逼的这个候选人理论上是十分牛逼的这个候选人理论上是十分牛逼的',
		            InterviewResult:'1',
		            Attachment:'面试简历.doc'
		          }, {
		            DateStart: '2018/1/23  09:00',
		            Name: '闫小晶',
		            SchoolName:'哈佛',
		            Major:'计算机',
		            Degree:8,
		            Email:'a224dggss@163.com',
		            isOnline:false,
		            Position:'产品经理',
		            Status: 1,
		            Score:5,
		            Comment:'这个候选人理论上是十分牛逼的这个候选人理论上是十分牛逼的这个候选人理论上是十分牛逼的这个候选人理论上是十分牛逼的这个候选人理论上是十分牛逼的',
		            InterviewResult:'1',
		            Sex:'2'
		          }, {
		            DateStart: '2018/1/25  09:00',
		            Name: '乔峰',
		           	SchoolName:'北大',
		            Major:'计算机',
		            Degree:4,
		            Email:'asddggst6677s@163.com',
		            isOnline:true,
		            Position:'产品经理',
		            Status: 2,
		            Score:5,
		            Comment:'这个候选人理论上是十分牛逼的这个候选人理论上是十分牛逼的这个候选人理论上是十分牛逼的这个候选人理论上是十分牛逼的这个候选人理论上是十分牛逼的',
		            InterviewResult:'1'
		          }, {
		            DateStart: '2017/9/25  09:00',
		            Name: '王小虎',
		            SchoolName:'家里蹲',
		            Major:'计算机',
		            Degree:3,
		            Email:'asddg12gss@163.com',
		            isOnline:false,
		            Position:'产品经理',
		            Status: 1,
		            Score:5,
		            Comment:'这个候选人理论上是十分牛逼的这个候选人理论上是十分牛逼的这个候选人理论上是十分牛逼的这个候选人理论上是十分牛逼的这个候选人理论上是十分牛逼的',
		            InterviewResult:'1'
		         },
		         {
		            DateStart: '2017/9/25  09:00',
		            Name: '周星驰',
		            SchoolName:'家里蹲',
		            Major:'计算机',
		            Degree:4,
		            Email:'asddg12gss@163.com',
		            isOnline:false,
		            Position:'产品经理',
		            Status: 1,
		            Score:5,
		            Comment:'这个候选人理论上是十分牛逼的这个候选人理论上是十分牛逼的这个候选人理论上是十分牛逼的这个候选人理论上是十分牛逼的这个候选人理论上是十分牛逼的',
		            InterviewResult:'1'
		         }],
		         candidateInfo:{Sex:'1' , Attachment:{name:''}},
		         ivFile:'',
		         candidateListFileName:'',
		         candidateListFilefile:{},
		         ivBetweenInfo:{}
			}
		},
		components: {
			'el-botton': Button,
			'el-input': Input,
			'el-table': Table,
			'el-table-column': TableColumn,
			'el-pagination': Pagination,
			'el-dialog': Dialog,
			'el-form': Form,
			'el-form-item':FormItem,
			'el-radio': Radio,
			'el-radio-group': RadioGroup,
			'el-upload': Upload,
			'el-select': Select,
			'el-option': Option
		},
		methods:{
			handleSelectionChange(){},
			handleSizeChange(val) {
		        console.log(`每页 ${val} 条`);
		     },
	        handleCurrentChange(val) {
	        	console.log(`当前页: ${val}`);
	        },
	        openCandidateDiaLog(index , item){
	        	this.candidateInfo = item || {Name:'' , Sex:''};
	        	this.dialogFormVisible = true;
	        },
	        candidateDiaLogOk(){
	        	let fd = FormData();
	        	fd.append('file' , this.candidateInfo.Attachment);
	        	this.dialogFormVisible = false;
	        	//console.log(this.candidateInfo);
	        },
	        openGoOtherDiaLog(flag){
	        	this.goOtherTitle = flag ? '调整面试间' : '下一轮面试';
	        	this.goOtherDialogFormVisible = true;
	        },
	        goOtherDiaLogOk(){
	        	let fd = FormData();
	        	this.goOtherDialogFormVisible = false;
	        },
	        addInterviewBetween(){
	        	this.isShowAddIvBetween = true;
	        },
	        changeIV(){
	        	this.isShowAddIvBetween = false;
	        },
	        removeCandidate(){
	        	this.$confirm('是否将该候选人移除面试间?', '提示', {
		          confirmButtonText: '确定',
		          cancelButtonText: '取消',
		          type: 'warning'
		        }).then(() => {
		          this.$message({
		            type: 'success',
		            message: '移除成功!'
		          });
		        }).catch(() => {
		               
		        });
	        },
	        uploadFile(){
	        	let fileEl = this.$refs['file'];
	        	fileEl.value = '';
	        	fileEl.click();
	        },
	        upResumeChange(ev){
	        	this.ivFile = ev.target.files[0];
	        	this.$set(this.candidateInfo , 'Attachment' , this.ivFile.name);
	        },
	        toLeadCandidateList(){
	        	let fileEl = this.$refs['toLeadCandidateListRef'];
	        	fileEl.value = '';
	        	fileEl.click();
	        },
	        toLeadCandidateListChange(ev){
	        	this.candidateListFilefile = ev.target.files[0];
	        	this.candidateListFileName = this.candidateListFilefile.name;
	        },
	        opentoLeadCandidateDiaLog(){
	        	let fd = new FormData();
	        	fd.append('file' , this.candidateListFilefile);
	        	this.toLeadDialogFormVisible = true;
	        },
	        toLeadcandidateDiaLogOk(){
	        	this.toLeadDialogFormVisible = false;
	        },
	        toLeadcandidateDiaLogCancel(){
	        	this.toLeadDialogFormVisible = false;
	        },
	        openMessageDiaLog(){
	        	this.messageDialogFormVisible = true;
	        },
	        messageDiaLogOk(){

	        },
			getStatus(status){
				let result = '';
				switch(status){
					case 0:
						result ='<span style="color:#38c653">已接受面试</span>'
					break;
					case 1:
						result ='<span style="color:#fdb535">未通知面试</span>' 
					break;
					case 2:
						result ='<span style="color:#b5c2cb">已拒绝</span>'  
					break;
				}
				return result;
			}
		}
	}
</script>
<template>
<div>
	<div class="candidate_list">
		<div class="header">
		<img src="../assets/images/go-back.png">
			<span>面试间：产品经理岗</span>
			<el-input placeholder="搜索候选人" style="float: right; width:320px;height:42px;margin-top: -6px;">
				<i slot="prefix" class="el-input__icon el-icon-search"></i>
			</el-input>
		</div>
		<div class="operation">
			<el-botton type="primary" @click="openCandidateDiaLog">新增候选人</el-botton>
			<el-botton type="primary" @click="opentoLeadCandidateDiaLog">导入候选人</el-botton>
			<el-botton type="primary" @click="removeCandidate">移出面试间</el-botton>
			<el-botton type="primary" @click="openGoOtherDiaLog('isAdjust')">调整面试间</el-botton>
			<el-botton type="primary" @click="openMessageDiaLog">发送通知</el-botton>
			<el-botton type="primary" @click="openGoOtherDiaLog('')">下一轮面试</el-botton>
			<div class="download">
				<img src="../assets/images/download.png">
				<span>下载</span>
			</div>
			<div class="interview-set">
				<img src="../assets/images/interview-set.png">
				<span>面试间设置</span>
			</div>
		</div>
		<el-table :data="tableData" style="width: 100%" @selection-change="handleSelectionChange">
		    <el-table-column  type="selection">
      		</el-table-column>
      		<el-table-column  label="新增候选人" width="200" >
	      		<template slot-scope="scope">
		      		<div class="candidate">
		      			<div class="candidate-name">
			      			<span>{{scope.row.Name}}</span>
			      			<span>{{scope.row.isOnline ? '在线' : '未在线'}}</span>
			      		</div>
			      		<div>
			      			<span>{{scope.row.SchoolName + ' | '}}</span>
			      			<span>{{scope.row.Major + ' | '}}</span>
			      			<span>{{degreeDic[scope.row.Degree]}}</span>
			      		</div>
			      		<p>{{scope.row.Email}}</p>
			      	</div>
	      		</template>
      		</el-table-column>
      		<el-table-column prop="Position" label="投递职位" width="180">
      		</el-table-column>
      		<el-table-column prop="DateStart" label="面试时间">
      		</el-table-column>
      		<el-table-column label="状态">
	      		<template slot-scope="scope">
	      			<div v-html="getStatus(scope.row.Status)"></div>
	      		</template>
      		</el-table-column>
      		<el-table-column prop="remark" label="面试结果">
      			<template slot-scope="scope">
      				<span>{{scope.row.Score + '分 | ' + (scope.row.InterviewResult =='1' ? '通过' : '不通过') +' | '+ scope.row.Comment}}</span>
      			</template>
      		</el-table-column>
      		<el-table-column  label="操作">
      		<template slot-scope="scope">
		        <el-button
		          size="mini"
		          type="primary"
		          @click="handleEdit(scope.$index, scope.row)">立即面试</el-button>
		        <el-button
		          size="mini"
		          type="text"
		          @click="openCandidateDiaLog(scope.$index, scope.row)">修改</el-button>
		    </template>
      		</el-table-column>
    	</el-table>
    	<el-pagination
		  :background="background"
		  :pageSize="pageSize"
		  layout="prev, pager, next , jumper , -> , slot"
		  :total="1000"
		  prev-text="上一页"
		  next-text="下一页" 
		  @size-change="handleSizeChange"
      	  @current-change="handleCurrentChange"
		  class="pagination"
		  >
		</el-pagination>
	</div>
	<el-dialog title="新增候选人" :visible.sync="dialogFormVisible" width="538px">
	  <el-form :model="candidateInfo">
	    <el-form-item label="姓名" :label-width="formLabelWidth" prop="Name" required style="display: inline-block;">
	      <el-input v-model="candidateInfo.Name" auto-complete="off" style="width: 260px;"></el-input>
	    </el-form-item>
	    <el-form-item prop="Sex" style="display: inline-block;margin-left: 15px;">
		    <el-radio-group v-model="candidateInfo.Sex">
		      <el-radio label="1">男</el-radio>
		      <el-radio label="2">女</el-radio>
		    </el-radio-group>
		</el-form-item>
	    <el-form-item label="手机号" :label-width="formLabelWidth" prop="Mobile" required>
	      <el-input v-model="candidateInfo.Mobile" auto-complete="off" style="width: 260px;"></el-input>
	    </el-form-item>
	    <el-form-item label="电子邮箱" :label-width="formLabelWidth" prop="Email" required>
	      <el-input v-model="candidateInfo.Email" auto-complete="off" style="width: 260px;"></el-input>
	    </el-form-item>
	    <el-form-item label="投递岗位" :label-width="formLabelWidth" prop="Position" required>
	      <el-input v-model="candidateInfo.Position" auto-complete="off" style="width: 260px;"></el-input>
	    </el-form-item>
	    <el-form-item :label-width="formLabelWidth" required>
	      	<el-input readonly :value="candidateInfo.Attachment"  auto-complete="off" style="display: inline-block;width: 260px;"></el-input>
	      	<div style="display: inline-block;margin-left: 20px;">
		      	<input 
		      		type="file"
		      		ref="file"
		      		style="display: none;"
		      		@change="upResumeChange"
		      	>
		      	<span @click="uploadFile" style="color: #409EFF;cursor: pointer;">上传文件</span>
	      	</div>
	      	</el-upload>
	      	<p style="color:#b5c2cb;">支持doc、docx、pdf、png、jpg、jpeg、gif 格式，4M以内</p>
	    </el-form-item>
	     <el-form-item label="毕业院校" :label-width="formLabelWidth" prop="SchoolName"  required>
	      <el-input v-model="candidateInfo.SchoolName" auto-complete="off" style="width: 260px;"></el-input>
	    </el-form-item>
	     <el-form-item label="所学专业" :label-width="formLabelWidth" prop="Major" required>
	      <el-input v-model="candidateInfo.Major" auto-complete="off" style="width: 260px;"></el-input>
	    </el-form-item>
	    <el-form-item label="最高学历" prop="Degree" :label-width="formLabelWidth">
	      <el-select v-model="candidateInfo.Degree" placeholder="请选择最高学历" style="width: 260px;">
	        <el-option :label="value" :value="key - 0" v-for="(value , key) in degreeDic" :key="value"></el-option>
	      </el-select>
	    </el-form-item>
	  </el-form>
	  <div slot="footer" class="dialog-footer">
	    <el-button type="primary" @click="candidateDiaLogOk">继续添加下一个</el-button>
	    <el-button  @click="candidateDiaLogOk">进入下一步</el-button>
	  </div>
	</el-dialog>
	<el-dialog :title="goOtherTitle" :visible.sync="goOtherDialogFormVisible" width="538px">
		<div style="margin: 0 0 20px 25px;">
			<span style="margin-right: 10px;">请选择面试间</span>
			<el-select v-model="candidateInfo.region" placeholder="请选择面试间" @change="changeIV">
		        <el-option label="面试间1" value="0"></el-option>
		        <el-option label="面试间2" value="1"></el-option>
		        <el-option label="面试间3" value="2"></el-option>
		        <el-option label="面试间4" value="3"></el-option>
		        <el-option label="面试间5" value="4"></el-option>
		        <el-option label="面试间6" value="5"></el-option>
		      </el-select>
		      <div style="display: inline-block; color:#409EFF;cursor: pointer;margin-left: 10px;" @click="addInterviewBetween">
		      	<img src="">
		      	<span>新增</span>
		      </div>
	      </div>
	  <el-form :model="ivBetweenInfo" :style="{display:isShowAddIvBetween ? 'block' : 'none'}">
	    <el-form-item label="面试间名称" :label-width="formLabelWidth" required style="display: inline-block;">
	      <el-input v-model="ivBetweenInfo.Name" auto-complete="off" style="width: 310px;"></el-input>
	    </el-form-item>
	    <el-form-item label="面试开始时间" :label-width="formLabelWidth" required>
	        <el-date-picker
		      v-model="ivBetweenInfo.StartTime"
		      type="datetime"
		      placeholder="选择日期时间"
		      align="right"
		      format="yyyy-MM-dd HH:mm"
		      value-format="yyyy-MM-dd HH:mm"
		    >
	    	</el-date-picker>
	    </el-form-item>
	    <el-form-item label="每人面试时长" :label-width="formLabelWidth" required>
	    	<el-input-number v-model="ivBetweenInfo.ivRange" :step="1"></el-input-number>
	    	<span style="margin-left: 10px;">分钟</span>
	    		    <p>预计16:00:00面试结束</p>
	    </el-form-item>
	    <el-form-item label="投递岗位" :label-width="formLabelWidth" required>
	      <el-input v-model="ivBetweenInfo.name" auto-complete="off" style="width: 310px;"></el-input>
	    </el-form-item>
	    <el-form-item label="面试官" :label-width="formLabelWidth" required>
		    <el-radio-group v-model="ivBetweenInfo.Sex">
		    <el-radio label="1">男</el-radio>
		    <el-radio label="2">女</el-radio>
		</el-radio-group>
		</el-form-item>
	     <el-form-item :label-width="formLabelWidth" required>
	      <el-input v-model="ivBetweenInfo.name" auto-complete="off" placeholder="昵称" style="width: 310px;"></el-input>
	    </el-form-item>
	     <el-form-item :label-width="formLabelWidth" required>
	      <el-input v-model="ivBetweenInfo.name" auto-complete="off" placeholder="面试官邮箱" style="width: 310px;"></el-input>
	    </el-form-item>
	  </el-form>
	  <div slot="footer" class="dialog-footer">
	    <el-button type="primary" @click="goOtherDiaLogOk" >确定</el-button>
	    <el-button  @click="goOtherDialogFormVisible = false">取消</el-button>
	  </div>
	</el-dialog>
	<el-dialog title="导入候选人" :visible.sync="toLeadDialogFormVisible" width="538px">
		<div style="margin-left: 20px;color:#89949b;">
			<p style="color:#486d82;line-height:26px;text-align:left;">导入说明</p>
			<p style="margin-top: 5px;">1.导入前，请先下载倒入模版，将候选人信息整理完成后再导入；</p>
			<p>2.本面试间设置最多可导入x个候选人，超出部分无法导入；</p>
			<p>3.文件大小请控制在2M以内。</p>
			<a href="" style="text-decoration: none;color:#408ee6;margin-top: 20px;display: inline-block;">下载倒入模版</a>
			<div style="margin-top: 20px;">
				<span style="margin-right: 10px;color:#89949b;">上传文件</span>
				<el-input readonly style="display: inline-block;width: 260px;" :value="candidateListFileName"></el-input>
				<input type="file" style="display: none;" ref="toLeadCandidateListRef" @change="toLeadCandidateListChange">
				<span @click="toLeadCandidateList" style="color:#408ee6;cursor: pointer;margin-left: 20px;">浏览</span>
			</div>
		</div>
		<div slot="footer" class="dialog-footer">
		    <el-button type="primary" @click="candidateDiaLogOk" >上传</el-button>
		    <el-button @click="toLeadDialogFormVisible = false" >取消</el-button>
	  </div>
	</el-dialog>
	<el-dialog title="通知" :visible.sync="messageDialogFormVisible" width="538px">
	<img src="../assets/images/message.png" style="vertical-align: top;margin-right: 2px;">
		<span style="color:#fdb535;display: inline-block;width: 428px;">本次通知，将扣除您x个面试点数，若候选人实际未上线，则会给您相应数量的补偿。</span>
		<div style="width: 455px;margin-top: 20px;">
			<span style="color:#89949b;">通知时的模版内容</span>
			<span style="font-size:12px;color:#b5c2cb;float: right;">[  ] 内部分不需要手动输入，系统会自动抓取对应信息</span>
		</div>
		<div style="border:1px solid #e3e4e8;width:434px;border-radius:5px;margin-top: 10px;padding:15px 0 19px 21px;">
			<div>亲爱的<span style="color:#fdb535;">[姓名]：</span></div>
			<div style="margin-left: 30px;line-height: 30px;">经过慎重的考虑，您非常符合我们公司的招聘要求，特邀请您参</div>
			<div style="margin-left: 30px;line-height: 30px;">加[公司名称] - [岗位]的在线面试，请您务必准时参加！</div>
			<div style="margin-left: 30px;line-height: 30px;">面试时间：<span style="color:#fdb535;">[面试时间]</span></div>
			<div style="margin-left: 30px; line-height: 30px;"> 面试间链接：<span style="color:#fdb535;">[面试地址]</span></div>
			<div style="margin-left: 30px; line-height: 30px;"> 面试邀请码：<span style="color:#fdb535;">[面试邀请码]</span></div>
		</div>
		<div slot="footer" class="dialog-footer">
	    <el-button type="primary" @click="messageDiaLogOk">立即通知</el-button>
	    <el-button  @click="messageDialogFormVisible = false">取消</el-button>
	  </div>
	</el-dialog>
	<el-dialog title="修改面试时间" :visible.sync="updateTimeDialogFormVisible" width="538px">
	  <el-form :model="ivBetweenInfo" :style="{display:isShowAddIvBetween ? 'block' : 'none'}">
	  	<el-form-item label="请选择面试时间段">
		  	<el-select v-model="candidateInfo.region" placeholder="请选择面试间" @change="changeIV">
			        <el-option label="9:30-10:00" value="0"></el-option>
			        <el-option label="9:30-10:00" value="1"></el-option>
			        <el-option label="9:30-10:00" value="2"></el-option>
			        <el-option label="9:30-10:00" value="3"></el-option>
			        <el-option label="9:30-10:00" value="4"></el-option>
			        <el-option label="9:30-10:00" value="5"></el-option>
			</el-select>
		</el-form-item>
	    <el-form-item label="面试开始时间" :label-width="formLabelWidth" required>
	        <el-date-picker
		      v-model="ivBetweenInfo.StartTime"
		      type="datetime"
		      placeholder="选择日期时间"
		      align="right"
		      format="yyyy-MM-dd HH:mm"
		      value-format="yyyy-MM-dd HH:mm"
		    >
	    	</el-date-picker>
	    </el-form-item>
	    <el-form-item label="面试时长" :label-width="formLabelWidth" required>
	    	<el-input-number v-model="ivBetweenInfo.ivRange" :step="1"></el-input-number>
	    	<span style="margin-left: 10px;">分钟</span>
	    	<p>预计16:00:00面试结束</p>
	    </el-form-item>
	  </el-form>
	  <div slot="footer" class="dialog-footer">
	    <el-button type="primary" @click="goOtherDiaLogOk" >确定</el-button>
	    <el-button  @click="goOtherDialogFormVisible = false">取消</el-button>
	  </div>
	</el-dialog>
</div>
</template>
<style lang="less" scoped>
	.candidate_list{
		.header{
			background:#e4ecf7;
			padding: 22px 30px;
			font-family:MicrosoftYaHei;
			font-size:18px;
			color:#486d82;
			text-align:left;
			img{
				vertical-align: middle;
			}
			span{
				vertical-align: middle;
			}
		}
		.operation{
			padding: 22px 30px;
			border-bottom: 3px solid #e0eff9;
			.interview-set , .download{
				cursor: pointer;
				display: inline-block;
				float: right;

				color:#3ca0ec;
				img , span{
					vertical-align: middle;
				}
			}
			.download{
				margin-left: 20px;
			}
		}
		.candidate{
			font-size: 14px;
			color:#b5c2cb;
			.candidate-name{
				color:#3ca0ec;
				font-size: 16px;
				span:last-child{
					font-size:12px;
					padding: 2px;
					border:1px solid #3ca0ec;
					border-radius:2px;
				}
			}
		}
		.pagination{
			float: right;
			margin-right: 30px;
			margin-top: 30px;
		}
	}
</style>
<style lang="less">
	.candidate_list{
		.header{
			input{
				border-radius:100px;
			}
		}
	}
</style>



